.register-background {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #1f1c2c, #928dab);
  padding: 20px;
}

.register-container {
  background: rgba(255, 255, 255, 0.9);
  padding: 40px;
  border-radius: 16px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.register-form .form-title {
  text-align: center;
  color: #1f1c2c;
  margin-bottom: 24px;
  font-size: 24px;
  font-weight: bold;
}

.register-form input {
  border-radius: 8px;
  padding: 10px;
}

.register-footer {
  text-align: center;
  margin-top: 10px;
  margin-bottom: 16px;
  font-size: 14px;
}

.register-footer a {
  color: #4f46e5;
  font-weight: 500;
  text-decoration: none;
}

.btn-submit {
  width: 100%;
  padding: 12px;
  background-color: #4f46e5;
  color: white;
  border: none;
  border-radius: 25px;
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.btn-submit:hover {
  background-color: #3730a3;
}
