.custom-navbar {
  background: linear-gradient(135deg, #1f1c2c, #928dab);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
  padding: 10px 0;
  transition: all 0.3s ease-in-out;
}

.navbar-container {
  max-width: 1200px;
  margin: auto;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar-brand {
  font-size: 24px;
  font-weight: bold;
  color: black;
  text-decoration: none;
  transition: color 0.3s ease;
}


.navbar-links {
  display: flex;
  align-items: center;
  gap: 20px;
}

.navbar-user {
  font-size: 16px;
  color: black;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.navbar-user:hover {
  color: #4f46e5;
}

.navbar-btn {
  padding: 8px 16px;
  background-color: #4f46e5;
  color: white;
  border: none;
  border-radius: 20px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.navbar-btn:hover {
  background-color: #3730a3;
}
