.login-container {
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #1f1c2c, #928dab);
  padding: 20px;
}

.login-card {
  width: 100%;
  max-width: 400px;
  padding: 40px 30px;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  background-color: #fff;
}

.login-title {
  text-align: center;
  margin-bottom: 10px;
  font-weight: 700;
  color: #1f1c2c;
}

.login-form .ant-form-item {
  margin-bottom: 20px;
}

.login-button {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border-radius: 6px;
  margin-top: 10px;
}

.register-link {
  display: block;
  text-align: center;
  margin-top: 15px;
  font-size: 14px;
}
