/* Background and base styling */
body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(135deg, #1f1c2c, #928dab);
  min-height: 100vh;
  color: #f1f1f1;
}

/* Filter Section */
.filters {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  margin: 20px;
  padding: 20px;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(8px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3);
  color: #ffffff;
}

.filters h5 {
  margin-bottom: 8px;
  color: #e0e0e0;
}

.filters .ant-select,
.filters .ant-picker {
  min-width: 160px;
}

/* Icons */
.switch-icon {
  display: flex;
  align-items: center;
  font-size: 20px;
}

.active-icon {
  color: #4ade80; /* Green */
  cursor: pointer;
}

.inactive-icon {
  color: #d1d5db;
  cursor: pointer;
}

/* Buttons */
.btn-primary {
  background-color: #4f46e5;
  color: #fff;
  border: none;
  padding: 10px 16px;
  font-weight: bold;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
}

.btn-primary:hover {
  background-color: #3730a3;
  transform: scale(1.05);
}

/* Content Table */
.content {
  margin: 20px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  backdrop-filter: blur(6px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
}

/* Ant Design Table Styling */
.ant-table {
  background-color: rgba(255, 255, 255, 0.05) !important;
  color: #f9f9f9;
}

.ant-table-thead > tr > th {
  background: rgba(0, 0, 0, 0.4) !important;
  color: #f1f1f1 !important;
  font-weight: bold;
}

.ant-table-tbody > tr > td {
  background: rgba(255, 255, 255, 0.02) !important;
  color: #f1f1f1;
}

/* Modal Styling */
.ant-modal-content {
  background: rgba(255, 255, 255, 0.95) !important;
  border-radius: 16px !important;
}

.ant-modal-title {
  color: #1f1c2c !important;
  font-size: 20px;
  font-weight: bold;
}

/* Form Inside Modal */
.ant-form-item-label > label {
  color: #1f1c2c !important;
  font-weight: 500;
}

.ant-input,
.ant-select-selector {
  border-radius: 10px !important;
  padding: 8px 12px !important;
}

/* Edit/Delete Icons */
.anticon {
  font-size: 18px;
  cursor: pointer;
  color: #4f46e5;
  transition: 0.2s;
}

.anticon:hover {
  color: #1e3a8a;
}

/* Responsive */
@media (max-width: 768px) {
  .filters {
    flex-direction: column;
  }

  .content {
    padding: 10px;
  }
}
