.card {
  margin-bottom: 20px;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.05);
  color: #ffffff;
  box-shadow: 0 4px 18px rgba(0, 0, 0, 0.3);
  border: none;
}

.card-header {
  font-weight: 600;
  font-size: 18px;
  background: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding: 10px 16px;
}

.card-body {
  padding: 12px 16px;
}

.card h5 {
  font-size: 16px;
  margin-bottom: 8px;
}

.row.mt-3 {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: flex-start;
}

.col-md-4 {
  flex: 1 1 30%;
  min-width: 280px;
}
