.footer {
  background-color: #1c1c1c;
  color: #fff;
  padding: 30px 20px 10px;
  width: 100%;
  box-shadow: 0 -4px 10px rgba(0, 0, 0, 0.4);
  margin-top: auto;
}

.footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}
.content-wrapper {
  margin-bottom: 200px; 
}
.footer-section {
  flex: 1;
  min-width: 200px;
  margin: 10px 20px;
}

.footer-section h5,
.footer-section h6 {
  margin-bottom: 10px;
  font-weight: 600;
}

.footer-section p,
.footer-section ul,
.footer-section a {
  font-size: 14px;
  color: #ccc;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  margin-bottom: 8px;
}

.footer-section a {
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-section a:hover {
  color: #fff;
}

.footer-section.social a {
  margin-right: 10px;
  font-size: 18px;
  color: #ccc;
}

.footer-bottom {
  text-align: center;
  margin-top: 15px;
  font-size: 13px;
  border-top: 1px solid #333;
  padding-top: 10px;
}
